import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { ServiceId } from '@core/constants/service-id.const';
import { ScreenDetectorService } from '@services/screen-detector.service';
import { BehaviorSubject } from 'rxjs';
import { TabUtils } from '@services/modules/service-tab/tab.utils';
import { ServiceTab } from '@schemas/modules/service-tabs/service-tab';

@Component({
    selector: 'tz-tab',
    templateUrl: './tz-tab.component.html',
    styleUrls: ['./tz-tab.component.scss'],
})
export class TzTabComponent implements OnInit, OnChanges {
    @Input() tab!: ServiceTab;
    @Input() selected = false;
    @Input() enabled = true;
    @Input() removable = true;
    @Input() summaryActive = false;
    @Input() summaryLoading = false;
    @Input() first = false;
    @Input() last = false;
    @Output() tabRemoved: EventEmitter<void> = new EventEmitter();
    @Output() tabClicked: EventEmitter<void> = new EventEmitter();

    public disabled = false;
    public isDisplayedCartDataPrice = false;
    public isDisplayedMinimalPriceFromPreSearch = false;
    public isDisplayedNoResult = false;
    public isDisplayedNotSelected = false;
    public isMobile$: BehaviorSubject<boolean>;

    constructor(private screenSizeDetectorService: ScreenDetectorService) {
        this.isMobile$ = this.screenSizeDetectorService.isMobile;
    }

    ngOnInit(): void {
        //console.log('init tab');
        this.initializeTabData();
    }

    public onTabRemoved(): void {
        this.tabRemoved.emit();
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes['tab']) {
            //console.log('tab changed');
            if (changes['tab'] && changes['tab'].currentValue && !changes['tab'].isFirstChange()) {
                this.initializeTabData();
            }
        }
    }

    public getUrlName(serviceId: ServiceId): string {
        switch (serviceId) {
            case ServiceId.TRAIN:
                return 'trains';
            case ServiceId.FLIGHT:
                return 'flights';
            case ServiceId.FLIGHT_TRAIN:
                return 'flights';
            case ServiceId.ACC:
                return 'hotels';
            case ServiceId.ACC_DETAILS:
                return 'hotel';
            case ServiceId.CAR_RENTAL:
                return 'car-rentals';
            case ServiceId.TRANSFER:
                return 'transfers';
        }

        return '';
    }

    public onTabClicked(): void {
        if (!this.disabled) {
            this.tabClicked.emit();
        }
    }

    private initializeTabData(): void {
        this.isDisplayedMinimalPriceFromPreSearch = TabUtils.displayMinimalPriceFromPreSearch(this.tab);
        this.isDisplayedCartDataPrice = TabUtils.displayPriceFromCart(this.tab);
        this.isDisplayedNoResult = TabUtils.displayNoResult(this.tab);
        this.isDisplayedNotSelected = this.displayNotSelected();
        this.disabled = this.checkDisability();
    }

    private displayNotSelected(): boolean {
        return !this.tab.selectedService && this.tab.serviceId !== ServiceId.SUMMARY;
    }

    private checkDisability(): boolean {
        return this.tab.disable || this.summaryLoading;
    }
}
