import { Component, Input, OnChanges, OnDestroy, OnInit, SimpleChanges } from '@angular/core';
import { TabRoutingService } from '@services/searchEngine/services/tab-routing.service';
import { ListenersService } from '@services/modules/core/listeners-service';
import { ServiceId } from '@core/constants/service-id.const';
import { ListenerId } from '@core/constants/listener-id.const';
import { CartService } from '@services/modules/cart/services/cart.service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { ToolsService } from '@core/utils/tools-service';
import { TzServiceTabs } from '../../tabsUtils/tz-tabs-service';
import { TripPlannerService } from '@services/searchEngine/services/trip-planner.service';
import { UserSearchService } from '@services/searchEngine/services/user-search-service';
import { StateFrom } from '@core/constants/state-from.const';
import { CheckDepService } from '@services/searchEngine/services/check-dep.service';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { ScrollService } from '@services/modules/core/scroll-service';
import { ICart } from '@schemas/modules/cart/cart';
import { CartRemoveService } from '@services/modules/cart/services/cart-remove-service';
import { TzCheckoutService } from '@services/modules/checkout/services/tz-checkout.service';
import { CheckoutCartServiceSummary } from '@schemas/modules/checkout/cart-summary';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { TranslateService } from '@ngx-translate/core';
import { ServiceTab } from '@schemas/modules/service-tabs/service-tab';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-service-navigation-bar',
    templateUrl: './service-navigation-bar.component.html',
    styleUrls: ['./service-navigation-bar.component.scss'],
})
export class ServiceNavigationBarComponent implements OnInit, OnChanges, OnDestroy {
    //@Input() tabs: any = [];
    //@Input() isMobile = false;
    @Input() active = ServiceId.FLIGHT_TRAIN;
    //@Output() tabChanged: EventEmitter<number> = new EventEmitter<number>();

    public tabRemovable = true;
    public cartSummaryInfo: { cartSummary: CheckoutCartServiceSummary[]; totalPersonsStr: string } = {
        cartSummary: [],
        totalPersonsStr: '',
    };
    public tabs: ServiceTab[] = [];
    public showSummaryLoader = false;

    private activeServiceId = ServiceId.INVALID;
    private repriceListenerId!: number;
    private cartChangeListenerId!: number;
    private tmpPriceSer!: number;
    private cartData: ICart | undefined;
    private resultItemStyle = 'result-tab-item';
    private serviceNotSelected = 'un-selected-service';
    private summaryCompleted = true;

    constructor(
        private tabRoutingService: TabRoutingService,
        private listenersService: ListenersService,
        private cartService: CartService,
        private searchDataService: SearchDataService,
        private tabService: TzServiceTabs,
        private userSearchService: UserSearchService,
        private tripPlannerService: TripPlannerService,
        private checkDepService: CheckDepService,
        private cartRoutingService: CartRoutingService,
        private scrollService: ScrollService,
        private cartRemoveService: CartRemoveService,
        private tzCheckoutService: TzCheckoutService,
        private translateService: TranslateService,
        public platformService: PlatformService
    ) {}

    ngOnInit(): void {
        this.initializeTabComponent();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if ((changes['active'] && changes['active'].currentValue) || changes['active'].currentValue === 0) {
            const active = changes['active'].currentValue;
            this.tabService.setActive(active);
            this.reformTabs(true);
        }
    }

    public ngOnDestroy(): void {
        this.listenersService.unregisterListener(this.tmpPriceSer);
        this.listenersService.unregisterListener(this.repriceListenerId);
        this.listenersService.unregisterListener(this.cartChangeListenerId);
    }

    public onTabRemoved(index: number): void {
        this.cartRemoveService.removeTheService(this.tabs[index].serviceId, '').then(
            (isRemoved: boolean) => {
                console.log('remove success');
            },
            (error) => {
                console.error(error);
            }
        );
    }

    public onTabCLicked(index: number): void {
        const tab = this.tabs[index];
        if (tab.active || tab.disable) {
            return;
        }
        this.scrollService.scrollToTop();

        this.tripPlannerService.isRunning(this.searchDataService.getCombId(), this.tripPlannerEnds.bind(this, tab));
    }

    public reformTabs(plannerCompleted?: boolean): void {
        const tabs = ToolsService.deepCopy(this.tabService.getTabs());

        let activeTab = {
            minPrice: 0,
        };

        this.tabs = [];
        const selectedServices: any = this.searchDataService.getSelectedServicesMap();
        if (plannerCompleted !== undefined) {
            this.summaryCompleted = plannerCompleted;
        }
        this.showSummaryLoader = false;
        tabs.forEach((tab: any, index: number) => {
            tab.selectedService = selectedServices && selectedServices[tab.serviceId];
            const res = this.tabService.reformTabFields(tab, +this.active, this.tabRoutingService.areAllBooked());
            if (tab.loader) {
                this.showSummaryLoader = true;
            }
            if (res.active) {
                activeTab = res.tab;
                this.activeServiceId = res.tab.serviceId;
            }
            tab.selectedService = selectedServices && selectedServices[tab.serviceId];
            if (
                tab.active ||
                (!tab.loader &&
                    (tab.preSearch ||
                        ((tab.serviceId === ServiceId.SUMMARY || tab.resultCount > 0) && tab.minPrice > 0))) ||
                (tab.selectedService && tab.resultCount === 0 && tab.minPrice === 0)
            ) {
                tab.styles.push(this.resultItemStyle);
            }
            if (!tab.selectedService) {
                tab.styles.push(this.serviceNotSelected);
            }
            if (tab.active) {
                tab.styles.push('active-tab');
            }
            this.tabs.push(tab);
        });

        if (+this.active === ServiceId.SUMMARY) {
            this.activeServiceId = ServiceId.SUMMARY;
        }

        this.setSummaryTab(activeTab);
        this.cartData = this.cartService.getCartData();
        this.tabRemovable = this.cartSerList(this.cartData?.items).length >= 2;
        this.updateCartSummary();
    }

    private tripPlannerEnds(tab: any): void {
        if (!tab.enable && !tab.selectedService) {
            this.tabRoutingService.addService(tab.serviceId);
            this.userSearchService.searchCheckDep(tab.serviceId, true, true, false, false);
        } else if ((!tab.enable && tab.selectedService) || (tab.enable && tab.serviceId !== ServiceId.SUMMARY)) {
            //&& tab.resultCount === 0) {
            if (this.tabRoutingService.isCheckDepDirty()) {
                this.checkDepService.reCheckDep().then(
                    () => {
                        this.cartRoutingService.changeState(tab.serviceId, undefined, true, StateFrom.TAB);
                    },
                    () => {
                        this.cartRoutingService.changeState(tab.serviceId, undefined, true, StateFrom.TAB);
                    }
                );
            } else {
                this.cartRoutingService.changeState(tab.serviceId, undefined, true, StateFrom.TAB);
            }
        } else if (tab.serviceId === ServiceId.SUMMARY) {
            this.cartRoutingService.goToState(tab.serviceId, undefined, false, StateFrom.TAB);
        }
    }

    private initializeTabComponent(): void {
        this.reformTabs(true);
        if (!this.repriceListenerId) {
            this.repriceListenerId = this.listenersService.registerListener(
                [ListenerId.LISTEN_REPRICED, ListenerId.LISTEN_TAB_CHANGED],
                this.reformTabs,
                this
            );
        }
        //this.listenerId = this.listenersService.registerListener(ListenerId.LISTEN_FILTER, this.reformTabs, this);
        this.tmpPriceSer = this.listenersService.registerListener(ListenerId.LISTEN_TMP_PRICE, this.setTmpPrice, this);
        this.cartChangeListenerId = this.listenersService.registerListener(
            ListenerId.LISTEN_CART_CHANGED,
            this.reformTabs,
            this
        );
    }

    private cartSerList(cartItems: any) {
        let serviceIdList: ServiceId[] = [];
        if (!cartItems) {
            return serviceIdList;
        }
        for (const cartItem of cartItems) {
            serviceIdList.push(cartItem.serviceId);
        }
        serviceIdList = serviceIdList.filter((item, pos) => serviceIdList.indexOf(item) === pos);
        return serviceIdList;
    }

    private setSummaryTab(activeTab: any) {
        const summaryTab = this.tabService.getSummaryTab();
        summaryTab.loader = this.showSummaryLoader || !this.summaryCompleted;
        this.tabService.setSummaryActive(!summaryTab.loader);
        this.tabService.reformTabFields(summaryTab, +this.active, this.tabRoutingService.areAllBooked());
        summaryTab.minPrice = activeTab.minPrice;
        summaryTab.minPriceCur = activeTab.minPriceCur;
        summaryTab.enable = true;
        summaryTab.disable = false;
        if (this.summaryCompleted && +summaryTab.selectedItemPrice > 0) {
            summaryTab.styles.push(this.resultItemStyle);
        }
        if (summaryTab.active) {
            summaryTab.styles.push('active-tab');
        }

        if (!this.tabRoutingService.areAllBooked()) {
            summaryTab.disable = true;
            summaryTab.styles.push('un-validated-summary');
        }

        this.tabs.push({ ...summaryTab });
    }

    // SetTempPrice change on selection of classes
    private setTmpPrice(data: any) {
        if (data.reset) {
            this.reformTabs(true);
            return;
        }
        this.tabs.forEach((t: any) => {
            if (t.active && data.serviceId === t.serviceId) {
                t.minPrice = data.totalPrice;
                t.selectedItemPrice = data.totalPrice;
            }
        });
    }

    private updateCartSummary(): void {
        this.cartData = this.cartService.getCartData();
        if (!this.cartData) {
            return;
        }

        const travellersData = CartUtils.getTravellersData(this.cartData.items);
        if (!travellersData) {
            return;
        }

        const personStr = this.translateService
            .instant('TRAVELLER.' + (travellersData.total > 1 ? 'PERSONS' : 'PERSON'))
            .toLowerCase();

        this.cartSummaryInfo = {
            cartSummary: this.tzCheckoutService.formCheckoutCartSummary(this.cartData.items),
            totalPersonsStr: `${this.translateService.instant('BOOKINGS.SUMMARY.TRAVEL_OPTIONS_FOUND')} ${
                travellersData.total
            } ${personStr}`,
        };
    }
}
