import { Component, Inject, Input, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ServiceSummerColorfulIcons, ServiceSummeryWhiteIcons } from '@core/constants/service-icons.const';
import { ServiceTab } from '@schemas/modules/service-tabs/service-tab';
import { ServiceId } from '@core/constants/service-id.const';
import { CheckoutCartServiceSummary } from '@schemas/modules/checkout/cart-summary';
import { SelectedLuggageAmount } from '@schemas/modules/checkout/transport-luggage';

@Component({
    selector: 'tz-mob-summary-list',
    templateUrl: './mob-summary-list.component.html',
    styleUrls: ['./mob-summary-list.component.scss'],
})
export class MobSummaryListComponent implements OnInit {
    @Input() public luggageAmount!: SelectedLuggageAmount;
    @Input() public additionalLuggages: { type: string; luggageText: string }[] = [];
    public tabsServiceIds: Set<number> = new Set<number>();
    public servicesIcons: { [key: number]: string } = ServiceSummeryWhiteIcons;
    public servicesColorfulIcons: { [key: number]: string } = ServiceSummerColorfulIcons;
    public baskedTab: ServiceTab | undefined;
    public currentService: ServiceId = ServiceId.INVALID;
    public cartSummary: CheckoutCartServiceSummary[] = [];
    public serviceIDs = ServiceId;
    public totalPersonsStr = '';

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private mdDialogRef: MatDialogRef<MobSummaryListComponent>
    ) {}

    public ngOnInit(): void {
        this.initData();
    }

    public closeThisDialog(): void {
        this.mdDialogRef.close();
    }

    private initData(): void {
        this.tabsServiceIds = this.data.tabsServiceIds;
        this.baskedTab = this.data.baskedTab;
        this.cartSummary = this.data.cartSummary;
        this.totalPersonsStr = this.data.totalPersonsStr;

        this.cartSummary.forEach((cartSummary: CheckoutCartServiceSummary) => {
            cartSummary.iconType = this.servicesColorfulIcons[cartSummary.serviceId];
        });
    }
}
