import { Component, Input, OnChanges, OnInit } from '@angular/core';
import { ServiceTab } from '@schemas/modules/service-tabs/service-tab';
import { CheckoutCartServiceSummary } from '@schemas/modules/checkout/cart-summary';
import { ServiceSummeryDarkIcons } from '@core/constants/service-icons.const';

@Component({
    selector: 'tz-mob-summery-button',
    templateUrl: './mob-summery-button.component.html',
    styleUrls: ['./mob-summery-button.component.scss'],
})
export class MobSummeryButtonComponent implements OnInit, OnChanges {
    @Input() public tabs: ServiceTab[] = [];
    @Input() public cartSummary: CheckoutCartServiceSummary[] = [];
    public tabsServiceIds: Set<number> = new Set<number>();
    public servicesIcons: { [key: number]: string } = ServiceSummeryDarkIcons;
    public baskedTab: ServiceTab | undefined;

    get isMultipleServiceSelected(): boolean {
        const cartSummary = this.cartSummary;
        if (cartSummary.length <= 1) {
            return false;
        }

        let isMultiple = false;
        const { serviceId } = cartSummary[0];
        cartSummary.map((cartSummaryItem) => {
            if (cartSummaryItem.serviceId !== serviceId) {
                isMultiple = true;
            }
        });

        return isMultiple;
    }

    public ngOnChanges(): void {
        this.initData();
    }

    public ngOnInit(): void {
        this.initData();
    }

    private initData(): void {
        this.baskedTab = this.tabs.find((serviceTab: ServiceTab) => serviceTab.serviceId === 8);

        this.cartSummary.forEach((dataCart: CheckoutCartServiceSummary) => {
            this.tabsServiceIds.add(dataCart.serviceId);
        });
    }
}
