import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ListenerId } from '@core/constants/listener-id.const';
import { TranslateService } from '@ngx-translate/core';
import { ICart } from '@schemas/modules/cart/cart';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ListenersService } from '@services/modules/core/listeners-service';
import { SettingsService } from '@services/services/settings-service';
import { Subject } from 'rxjs';
import { skip, takeUntil } from 'rxjs/operators';
import { CartTransfers } from '@schemas/modules/cart/cart-transfers';

@Component({
    selector: 'tz-cart-summary-header',
    templateUrl: './cart-summary-header.component.html',
    styleUrls: ['./cart-summary-header.component.scss'],
})
export class CartSummaryHeaderComponent implements OnInit, OnDestroy {
    public cartData!: ICart;
    public personsStr = '';
    public transfers: CartTransfers[] = [];

    private repriceListenerId = -1;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private cartService: CartService,
        private router: Router,
        private translateService: TranslateService,
        private listenersService: ListenersService,
        private settingsService: SettingsService
    ) {}

    ngOnInit() {
        this.cartService.reInit().then((cartData: ICart) => {
            this.initializeBasketHeader(cartData);
            console.log('cartData', this.cartData);
        });

        this.repriceListenerId = this.listenersService.registerListener(
            ListenerId.LISTEN_REPRICED,
            () => {
                this.initializeBasketHeader(this.cartService.getCartData());
            },
            this
        );

        this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
            this.initializeBasketHeader(this.cartService.getCartData());
        });
    }

    ngOnDestroy(): void {
        this.listenersService.unregisterListener(this.repriceListenerId);
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public gotoCheckout(): void {
        this.router.navigate(['/checkout']);
    }

    private initializeBasketHeader(cartData: ICart): void {
        this.cartData = cartData;
        this.formTransfersOnlyContent();

        this.personsStr = this.cartService.getCartPersonsStr(cartData);
    }

    private formTransfersOnlyContent(): void {
        this.transfers = this.cartService.getTransfers(this.cartData);
    }
}
