import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { PlatformService } from '@services/platform.service';
import { ServiceId } from '@core/constants/service-id.const';
import { CheckoutCartServiceSummary } from '@schemas/modules/checkout/cart-summary';
import { ServiceTab } from '@schemas/modules/service-tabs/service-tab';
import { TabUtils } from '@services/modules/service-tab/tab.utils';
import { MobSummeryModalComponent } from '../../../results/components/mob-summery-modal/mob-summery-modal.component';
import { ModalService } from '@services/modal.service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';

enum TzTabComponentDirection {
    VERTICAL = 'vertical',
    HORIZONTAL = 'horizontal',
}

@Component({
    selector: 'tz-tabs',
    templateUrl: './tz-tabs.component.html',
    styleUrls: ['./tz-tabs.component.scss'],
})
export class TzTabsComponent implements OnInit {
    @Input() direction: string = TzTabComponentDirection.HORIZONTAL;
    @Input() tabs: ServiceTab[] = [];
    @Input() tabRemovable = false;
    @Input() cartSummaryInfo: { cartSummary: CheckoutCartServiceSummary[]; totalPersonsStr: string } = {
        cartSummary: [],
        totalPersonsStr: '',
    };

    @Output() tabClicked: EventEmitter<number> = new EventEmitter();
    @Output() tabRemoved: EventEmitter<number> = new EventEmitter();
    public summaryIndex = 4;
    public serviceId = ServiceId;
    public isDisplayedCartDataPrice = false;

    constructor(
        public platformService: PlatformService,
        public modalService: ModalService,
        public searchDataService: SearchDataService
    ) {}

    get isSummaryTabDisable(): boolean {
        const summaryTab: ServiceTab = this.getSummaryTab();

        return summaryTab.disable;
    }

    get isSummaryPage(): boolean {
        const summaryTab: ServiceTab = this.getSummaryTab();

        return summaryTab.active;
    }

    get summaryTab(): ServiceTab {
        return this.getSummaryTab();
    }

    ngOnInit(): void {
        this.initializeTabData();
    }

    public onTabClicked(index: number): void {
        this.tabClicked.emit(index);
    }

    public onTabRemoved(tabIndex: number): void {
        this.tabRemoved.emit(tabIndex);
    }

    public getTotalPrice(selectedItemPrice: number) {
        console.log(selectedItemPrice);
        return selectedItemPrice;
    }

    public async onOpenSummeryModal(cartSummary: CheckoutCartServiceSummary[], tabs: ServiceTab[]): Promise<any> {
        if (this.cartSummaryInfo.cartSummary.length < 2) {
            return;
        }

        await this.modalService.openSummeryModal(MobSummeryModalComponent, {
            data: {
                cartSummary,
                tabs,
                totalPersonsStr: this.cartSummaryInfo.totalPersonsStr,
            },
        });
    }

    private initializeTabData(): void {
        this.tabs.map((value) => {
            this.isDisplayedCartDataPrice = TabUtils.displayPriceFromCart(value);
        });
    }

    private getSummaryTab(): ServiceTab {
        let summaryTab!: ServiceTab;
        this.tabs.map((tab) => {
            if (tab.serviceId === this.serviceId.SUMMARY) {
                summaryTab = tab;
            }
        });

        return summaryTab;
    }
}
