import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ResultsComponent } from './results.component';
import { CartService } from '@services/modules/cart/services/cart.service';

const routes: Routes = [
    {
        path: '',
        component: ResultsComponent,
        children: [
            {
                path: 'fake', //does nothing, used to handle reload
                loadChildren: () => import('./fake/fake.module').then((m) => m.FakeModule),
            },
            {
                path: 'transfers/:country/:cityName',
                loadChildren: () => import('./transfers/transfers.module').then((m) => m.TransfersModule),
            },
            {
                path: 'transfers',
                loadChildren: () => import('./transfers/transfers.module').then((m) => m.TransfersModule),
            },
            {
                path: 'travel',
                loadChildren: () => import('./transport/transport.module').then((m) => m.TransportModule),
            },
            {
                path: 'hotels/:country/:cityName',
                loadChildren: () =>
                    import('./accommodations/accommodations.module').then((m) => m.AccommodationsModule),
            },
            {
                path: 'hotels',
                loadChildren: () =>
                    import('./accommodations/accommodations.module').then((m) => m.AccommodationsModule),
            },
            {
                path: 'hotel/:id/:city/:name',
                loadChildren: () => import('./accommodation/accommodation.module').then((m) => m.AccommodationModule),
            },
            {
                path: 'hotel',
                loadChildren: () => import('./accommodation/accommodation.module').then((m) => m.AccommodationModule),
            },
            {
                path: 'car-rentals/:country/:cityName',
                loadChildren: () => import('./car-rentals/car-rentals.module').then((m) => m.CarRentalsModule),
            },
            {
                path: 'car-rentals',
                loadChildren: () => import('./car-rentals/car-rentals.module').then((m) => m.CarRentalsModule),
            },
            {
                path: 'results-summary',
                loadChildren: () =>
                    import('./results-summary/results-summary.module').then((m) => m.ResultsSummaryModule),
                canActivate: [CartService],
            } /*,
            {
                path: '',
                pathMatch: 'full',
                redirectTo: 'travel',
            },
            {
                path: '**',
                redirectTo: 'travel',
            }*/,
        ],
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class ResultsRoutingModule {}
