import { Component, ElementRef, OnDestroy, OnInit, Renderer2, ViewChild } from '@angular/core';
import { Event, NavigationEnd, Router } from '@angular/router';
import { TzTabs } from '@shared/schemas/tz-tab';
import { TABS } from './tabs';
import { ServiceId } from '@core/constants/service-id.const';
import { CartService } from '@services/modules/cart/services/cart.service';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { LoaderService } from '@services/modules/core/loader.service';
import { ListenersService } from '@services/modules/core/listeners-service';
import { ListenerId } from '@core/constants/listener-id.const';
import { SearchErrorService } from '@services/searchEngine/services/search-error-service';
import { Subject } from 'rxjs';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { PlatformService } from '@services/platform.service';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { ResultsSharedServices } from '@services/modules/results/services/results.shared.services';
import { ModalService } from '@services/modal.service';
import { SearchComponent } from '../search/search.component';
import { ScrollService } from '@services/modules/core/scroll-service';
import { takeUntil } from 'rxjs/operators';
import { PageUtils } from '@core/utils/page-utils';
import { PagesUrls } from '@core/constants/pages-urls';
import { SettingsService } from '@services/services/settings-service';
import { StateFrom } from '@core/constants/state-from.const';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';

@Component({
    selector: 'tz-results',
    templateUrl: './results.component.html',
    styleUrls: ['./results.component.scss'],
})
export class ResultsComponent implements OnInit, OnDestroy {
    @ViewChild('serviceResultContainer', { read: ElementRef, static: false }) resultContainer!: ElementRef;

    public tabs: TzTabs = [];
    public currentService: ServiceId = ServiceId.INVALID;
    public editing = false;
    public selectedServices: { [key: string]: boolean } = {};
    public showSearchBar = true;
    public isSearchDropDownOpen = false;
    public isEditDisabled = true;
    public blockPage = false;
    public isSummary = false;
    public showScrollBtn = false;
    public scrollBtnRight = 22;
    public ACC_DETAILS = ServiceId.ACC_DETAILS;
    public discoverMode = false;
    public discoverImageLink = '';
    //public isHotelDetailsPage = false;

    private routerSubscription: any;
    private editListenerId = -1;
    private destroy$: Subject<boolean> = new Subject<boolean>();
    private isClickInside = false;
    private isSearchModalOpen = false;

    constructor(
        private router: Router,
        private scrollService: ScrollService,
        private cartService: CartService,
        private cartRouterService: CartRoutingService,
        private listenersService: ListenersService,
        private searchErrService: SearchErrorService,
        public loaderService: LoaderService,
        private searchDataService: SearchDataService,
        private renderer: Renderer2,
        public platformService: PlatformService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        private resultsSharedServices: ResultsSharedServices,
        private modalService: ModalService,
        private settingsService: SettingsService
    ) {
        this.tabs = TABS;
        this.routerSubscription = this.router.events.subscribe((event: Event) => {
            if (event instanceof NavigationEnd) {
                this.onStateChange(event.url);
            }
        });
    }

    ngOnInit(): void {
        this.cartRouterService.currentServiceId.subscribe((serviceId: ServiceId) => {
            this.isClickInside = false;
            this.editing = false;
            this.toggleEditing(false);
            this.searchDropDownToggle(false);
            this.handleActiveServiceIdChange(serviceId);
        });

        this.editListenerId = this.listenersService.registerListener(
            ListenerId.LISTEN_ENABLE_EDIT,
            this.toggleEditing.bind(this)
        );

        this.searchDataService.closeSearchEditingMode$.subscribe(() => {
            if (!this.isClickInside && !this.isSearchDropDownOpen) {
                this.toggleEditing(false);
            }
            this.isClickInside = false;
        });

        this.resultsSharedServices.pageBlocker.subscribe((block: boolean) => {
            console.log('receive page blocker to set to:', block);
            this.blockPage = block;
        });

        this.scrollBtnRight = ScrollService.adjustScrollButtonPosition(this.resultContainer);

        if (!this.platformService.isMobileDevice) {
            this.scrollService.onScroll$.pipe(takeUntil(this.destroy$)).subscribe((scrollElement) => {
                this.showScrollBtn = this.showSearchBar && scrollElement.scrollTop >= window.innerHeight * 1.5;
            });
        }

        this.platformService.isMobileDevice$.pipe(takeUntil(this.destroy$)).subscribe(() => {
            requestAnimationFrame(
                () => (this.scrollBtnRight = ScrollService.adjustScrollButtonPosition(this.resultContainer))
            );
        });
    }

    /*    ngAfterContentChecked() {
                    const element = this.renderer.selectRootElement('.wrapper', true);
            this.renderer.setStyle(element, 'overflow-y', 'overlay');
            this.renderer.setStyle(element, 'overflow-x', 'auto');
        }*/

    ngOnDestroy(): void {
        this.listenersService.unregisterListener(this.editListenerId);

        this.destroy$.next(true);
        this.destroy$.complete();
        this.routerSubscription.unsubscribe();
    }

    /*  public navigateToService(index: number): void {
        debugger;
        const tab = this.tabs[index];
        const service = tab.serviceId as ServiceId;
        const serviceUrl = SERVICE_ROUTE_MAP[service];
        this.cartRouterService.set(service);
        this.router.navigate([serviceUrl], {
            relativeTo: this.route,
        });
    }*/

    public toggleEditing(enable: boolean, $event?: any): void | boolean {
        if (enable && this.isEditDisabled) {
            this.dialogService.openSnackBar({
                message: this.translateService.instant('NOTIFS.EDIT_SEARCH_FORBIDDEN'),
            });
            if ($event) {
                $event.stopPropagation();
                $event.preventDefault();
                return false;
            }
            return;
        }
        if (!enable && this.editing && this.searchErrService.getInErrorFlag()) {
            this.searchErrService.searchErrHdl(true);
        }

        this.editing = enable;

        if (enable) {
            this.editSearch();
        }

        if (enable) {
            this.renderer.addClass(document.body, 'body-lock-scroll');
        } else {
            this.renderer.removeClass(document.body, 'body-lock-scroll');
        }
    }

    public searchChange(activate: boolean): void {
        this.loaderService.skeletonLoading = activate;
    }

    public searchDropDownToggle(isSearchDropDownOpen: boolean): void {
        this.searchDataService.isSearchDropDownOpen$.next(isSearchDropDownOpen);
        this.isSearchDropDownOpen = isSearchDropDownOpen;
    }

    public editSearch(): void {
        if (!this.platformService.isMobileDevice || this.isSearchModalOpen) {
            return;
        }

        this.isSearchModalOpen = true;
        this.toggleEditing(true);

        this.modalService
            .createMainModal(SearchComponent, {
                data: {
                    selectedServices: this.selectedServices,
                    editMode: true,
                    editing: this.editing,
                    currentService: this.currentService,
                },
                title: 'SEARCH.EDIT_SEARCH',
            })
            .then(() => {
                this.isSearchModalOpen = false;
                this.toggleEditing(false);
            });
    }

    public gotoRoute(routePath: string): void {
        this.router.navigate([routePath]);
    }

    public scrollToTop(): void {
        this.scrollService.scrollToTop();
    }

    private setEditingAccess() {
        this.isEditDisabled = this.cartService.isServicesBooked(this.currentService);
    }

    private handleActiveServiceIdChange(serviceId: ServiceId): void {
        if (serviceId === ServiceId.FLIGHT || serviceId === ServiceId.TRAIN || serviceId === ServiceId.FLIGHT_TRAIN) {
            this.currentService = ServiceId.FLIGHT_TRAIN;
        } /*else if (serviceId === ServiceId.ACC_DETAILS) {
            this.currentService = ServiceId.ACC;
        } */ else {
            this.currentService = serviceId;
        }

        this.showSearchBar = this.currentService !== ServiceId.SUMMARY;
        this.setEditingAccess();
    }

    private onStateChange(url: string) {
        this.isSummary = PageUtils.is(url, PagesUrls.BASKET);
        this.discoverMode = this.settingsService.isDiscoverMode;
        //this.getStateFromParamFromRouter();
        const destination = StorageManagerUtil.get('destination');
        if (destination) {
            this.discoverImageLink = destination.imageLink;
        }
    }

    private getStateFromParamFromRouter(): StateFrom | void {
        const state = this.router.getCurrentNavigation()?.extras?.state;
        if (state && state['extra']) {
            if (state['extra'].imageLink) {
                this.discoverImageLink = state['extra'].imageLink;
            }
        }
    }

    /*private urlChange(): void {
        this.router.events.subscribe((event: Event) => {
            if (event instanceof NavigationEnd) {
                this.checkIsHotelDetailsPage(event.url);
            }
        });
    }

    private checkIsHotelDetailsPage(url: string): void {
        this.isHotelDetailsPage = PageUtils.is(url, PagesUrls.HOTEL_DETAILS_ID);
    }*/
}
